.page {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #85144b;
}

.content-font {
    font-family: 'Gothic A1', sans-serif;
}

.yellow-color {
    color: #FFDC00;
}

.orange-color {
    color: #FF851B
}

.three-rem {
    font-size: 3rem;
}

.two-rem {
    font-size: 2rem;
}

.bold {
    font-weight: bold;
}

.btn {
    width: 8vw;
    height: 5vh;
    border: 1px solid #FF851B;
    cursor: pointer;
    border-radius: 2%;
}

.column-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.btn:hover {
    -webkit-transition: ease 0.5s;
    transition: ease 0.5s;
    background-color: #FF851B;
    color: black;
}

.row-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.width-100 {
    width: 100vw;
}

.margin-3 {
    margin: 3%;
}

.transition {
    -webkit-transition: ease 0.5s;
    transition: ease 0.5s;
}

.one-rem {
    font-size: 1.5rem;
}

@-webkit-keyframes blink-text {
    0% {
        font-family: 'Gothic A1', sans-serif;
        font-size: 2.5rem;
        color: rgba(255, 133, 27, 0);
        box-shadow: none 0 rgba(0, 0, 0, 0);
        box-shadow: initial 0 rgba(0, 0, 0, 0);
    }
    50% {
        font-family: 'Gothic A1', sans-serif;
        font-size: 2.5rem;
        color: rgba(255, 133, 27, 1);
        box-shadow: none 0 -1.2rem 0 rgba(200, 200, 255, 1);
        box-shadow: initial 0 -1.2rem 0 rgba(200, 200, 255, 1);
    }
    100% {
        font-family: 'Gothic A1', sans-serif;
        font-size: 2.5rem;
        color: rgba(255, 133, 27, 0);
        box-shadow: none 0 rgba(0, 0, 0, 0);
        box-shadow: initial 0 rgba(0, 0, 0, 0);
    }
    }

@keyframes blink-text {
    0% {
        font-family: 'Gothic A1', sans-serif;
        font-size: 2.5rem;
        color: rgba(255, 133, 27, 0);
        box-shadow: none 0 rgba(0, 0, 0, 0);
        box-shadow: initial 0 rgba(0, 0, 0, 0);
    }
    50% {
        font-family: 'Gothic A1', sans-serif;
        font-size: 2.5rem;
        color: rgba(255, 133, 27, 1);
        box-shadow: none 0 -1.2rem 0 rgba(200, 200, 255, 1);
        box-shadow: initial 0 -1.2rem 0 rgba(200, 200, 255, 1);
    }
    100% {
        font-family: 'Gothic A1', sans-serif;
        font-size: 2.5rem;
        color: rgba(255, 133, 27, 0);
        box-shadow: none 0 rgba(0, 0, 0, 0);
        box-shadow: initial 0 rgba(0, 0, 0, 0);
    }
    }

.blink-text {
    color: #FF851B;
    -webkit-animation: blink-text 4s infinite;
            animation: blink-text 4s infinite;
  }

.tect-font {
    font-size: 3rem;
}

.width-15{
    width:20vw;
}

.width-30 {
    width: 30vw;
}
.width-50 {
    width: 50vw;
}
.width-45 {
    width: 45vw;
}

.project-div {
    width: 50vw;
    height: 100vh;
}

.tech-btn {
    width: 5vw;
    height: 4vh;
    background-color: white;
    border-radius: 4%;
    margin: 0.3%;
    border-color: white;
}
.tech-divdiv{
    width: 100%;
    display: flex;
    justify-content: flex;
    flex-direction: row;
}
.width-100p {
    width: 100%;
}

.project-div img {
    width: 90%;
    height: 45%;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
}

a {
    text-decoration: none;
}

.margin-05{
    margin: 0.5%;
}

.width-90 {
